import React, { Component } from 'react';
import { Subscription } from 'rxjs';
import { Assets, AssetType, IAssetConfig } from '../../models/AssetConfig';
import { newSampleNftDetails } from '../../models/NftDetails';
import { EthereumService } from '../../services/EthereumService';
import { MessagingService } from '../../services/MessagingService';
import { CharacterNftService } from '../../services/CharacterNftService';
import Flip from '../Common/Flip';
import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import { CharacterSet } from './CharacterSet';
import { Character } from './Characters';
import '../Tables.scss';
import './Collectibles.scss';
import './Characters.scss';
import './CharacterAnim.scss';
import si1 from '../../assets/avatars/3.png';
import si2 from '../../assets/avatars/4.png';
import si3 from '../../assets/avatars/7.png';
import si4 from '../../assets/avatars/8.png';
import { Utils } from '../../services/Utils';
import { BigNumber } from '@ethersproject/bignumber';
import { ITime } from '../../services/Interfaces';

const gaby = newSampleNftDetails(
  'Gaby Schlather',
  si2,
  [
    { id: 0, display_type: '', trait_type: 'Badge 1', badge_type: 'Special', value: 'Founding mother' },
    { id: 1, display_type: '', trait_type: 'Badge 2', badge_type: 'Image', value: 'Red Pill' }
  ],
  0
);
const kevin = newSampleNftDetails(
  'Kevin Molz',
  si4,
  [{ id: 0, display_type: '', trait_type: 'Badge 1', badge_type: 'Image', value: 'Laser eyes' }],
  1
);
const mitch = newSampleNftDetails(
  'Mitch Drummey',
  si1,
  [{ id: 0, display_type: '', trait_type: 'Badge 1', badge_type: 'Stat', value: 'Lightning' }],
  2
);
const judy = newSampleNftDetails('Judy Yopp', si3, [], 3);

interface ICollectiblesProps {
  messagingService: MessagingService;
  ethereumservice: EthereumService;
}

interface ICollectiblesState {
  isLoaded: boolean;
  investAsset?: string;
  assets: CharacterNftService[];
  showDetail: boolean;
  amountToMint: number;
  launchTimeRemaining: ITime;
}

class Collectibles extends Component<ICollectiblesProps, ICollectiblesState> {
  subscription: Subscription;
  walletAddress?: string;
  chainId?: number;
  displayAssetTypes = [AssetType.Collectible];
  equipTargetAssetsTypes = [AssetType.Crop];
  equipTarget?: IAssetConfig;
  paymentTarget?: IAssetConfig | null;
  nftService?: CharacterNftService;
  characterLaunchTimer?: NodeJS.Timer;

  constructor(props: any) {
    super(props);

    this.state = {
      isLoaded: false,
      investAsset: '',
      assets: [],
      amountToMint: 1,
      showDetail: false,
      launchTimeRemaining: { days: '  ', hours: '  ', mins: '  ', secs: '  ', isComplete: false }
    };

    this.subscription = new Subscription();

    this.handleAmountToMint = this.handleAmountToMint.bind(this);
    this.handleAmountToMintChange = this.handleAmountToMintChange.bind(this);
    this.mintCharacter = this.mintCharacter.bind(this);
    this.getMintCost = this.getMintCost.bind(this);

    SwiperCore.use([Navigation]);
  }

  componentDidMount() {
    var eventSubscription = this.props.messagingService.events.subscribe(event => {
      this.refresh();
    });

    this.subscription.add(eventSubscription);

    this.refresh();
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  handleAmountToMint(e: any) {
    var amount = Math.min(20, Math.max(1, e.target.value));
    this.setState({ amountToMint: amount });
  }

  handleAmountToMintChange(changeBy: number) {
    this.setState({ amountToMint: this.state.amountToMint + changeBy });
  }

  mintCharacter() {
    this.nftService?.mint(this.state.amountToMint);
  }

  refresh() {
    if (
      this.props.ethereumservice.walletData.walletAddress &&
      this.props.ethereumservice.networkManager?.network?.chainId
    ) {
      if (
        this.props.ethereumservice.walletData.walletAddress !== this.walletAddress ||
        this.props.ethereumservice.networkManager!.network?.chainId !== this.chainId
      ) {
        this.walletAddress = this.props.ethereumservice.walletData.walletAddress;
        this.chainId = this.props.ethereumservice.networkManager!.network!.chainId;
        this.init();
      } else {
        this.update();
      }
    }
  }

  init() {
    if (
      this.props.ethereumservice.networkManager?.network &&
      this.props.ethereumservice.networkManager?.network?.assets.size > 0
    ) {
      let displayAssets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(
        config => config.assetConstants === Assets.CHARACTER
      );

      let equipTargets = [...this.props.ethereumservice.networkManager!.network!.assets.values()].filter(config =>
        this.equipTargetAssetsTypes.includes(config.assetConstants!.assetType!)
      );

      if (equipTargets.length > 1) {
        throw new Error('Multiple crops not supported for equipping collectibles');
      }
      this.equipTarget = equipTargets[0];

      this.paymentTarget = this.props.ethereumservice.networkManager!.network!.characterBoostPayConfig;

      var assetUpdatePromises = displayAssets.map(ac => {
        var asset = new CharacterNftService(
          ac,
          this.equipTarget!,
          this.props.messagingService,
          this.props.ethereumservice,
          this.paymentTarget
        );

        if (asset.config.assetConstants === Assets.CHARACTER) {
          this.nftService = asset;
        }

        return asset.update().then(() => {
          return asset;
        });
      });

      return Promise.all(assetUpdatePromises).then(assets => {
        this.setState({ assets: assets, isLoaded: true });
      });
    }
  }

  update() {
    this.state.assets.forEach(asset => {
      asset.update();
    });

    this.setState({ assets: this.state.assets });
  }

  getMintCost(): string {
    return Utils.formatUnits(this.nftService!.mintPrice?.mul(BigNumber.from(this.state.amountToMint)));
  }

  render() {
    var characterCount = this.state.assets.reduce((p, c) => p + c.nfts.length, 0);
    return (
      <section id="content" className="is-vcentered">
        <div className="is-max-fullhd1">
          {!this.props.ethereumservice.walletData.isConnected && (
            <span
              className="column meme is-half has-text-centered mt-4"
              style={{ cursor: 'pointer' }}
              onClick={() => this.props.ethereumservice.connectWallet()}>
              <h3 className="sub-title py-3">Connect Wallet</h3>
            </span>
          )}

          {this.props.ethereumservice.walletData.isConnected && this.state.isLoaded && (
            <div>
              <div className="characters">
                {characterCount > 0 && this.renderAssets()}
                {characterCount <= 0 && this.renderSampleAssets()}
              </div>
              <section className="my-6">
                <div className="container is-flex is-flex-direction-column">
                  <div className="columns is-centered">
                    <span className="column meme is-bordered is-half has-text-centered">
                      {characterCount > 0 && <h3 className="sub-title is-upper pt-3">Mint more farmland characters</h3>}
                      {characterCount <= 0 && (
                        <h3 className="sub-title is-upper pt-3">Mint your own farmland characters</h3>
                      )}
                      {!this.props.ethereumservice.networkManager?.network?.hasCollectibles && (
                        <h3 className="sub-title m-4">
                          <span>Only on Arbitrum</span>
                        </h3>
                      )}
                      {this.props.ethereumservice.networkManager?.network?.hasCollectibles && (
                        <div className="is-flex my-5">
                          <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center character-mint buy-buttons">
                            <ul className="action-control">
                              <li className="action-form">
                                <div className="field has-addons">
                                  <div className="control">
                                    <button
                                      className="button minus-button is-action"
                                      disabled={this.state.amountToMint <= 1}
                                      onClick={() => this.handleAmountToMintChange(-1)}>
                                      <span className="icon">
                                        <i className="fas fa-minus"></i>
                                      </span>
                                    </button>
                                  </div>
                                  <div className="control">
                                    <input
                                      className="input is-action"
                                      type="text"
                                      disabled={true}
                                      value={this.state.amountToMint}
                                    />
                                  </div>
                                  <div className="control">
                                    <button
                                      className="button plus-button is-action"
                                      disabled={this.state.amountToMint >= 20}
                                      onClick={() => this.handleAmountToMintChange(+1)}>
                                      <span className="icon">
                                        <i className="fas fa-plus"></i>
                                      </span>
                                    </button>
                                  </div>
                                </div>
                              </li>
                              <li>
                                <button
                                  className="button boost-button is-action is-fullwidth"
                                  onClick={this.mintCharacter}>
                                  Mint Characters
                                </button>
                              </li>
                            </ul>

                            <div className="is-flex is-flex-direction-column is-align-items-center is-justify-content-center mt-5">
                              <p>
                                <span className="mr-1">Cost {this.getMintCost()}</span>
                                ETH (+ gas)
                              </p>
                            </div>
                          </div>
                        </div>
                      )}
                    </span>
                  </div>
                </div>
              </section>
            </div>
          )}
        </div>
      </section>
    );
  }

  renderAssets() {
    return (
      <React.Fragment>
        {this.state.assets.map(a => (
          <CharacterSet key={a.config.contractAddress} equipTarget={this.equipTarget!} nftService={a}></CharacterSet>
        ))}
      </React.Fragment>
    );
  }

  renderSampleAssets() {
    return (
      <React.Fragment>
        <Swiper navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          <SwiperSlide>
            <Character
              nft={gaby}
              actions={[{ name: 'Sample', isApproved: true, onClick: () => {}, onHoverMessage: null }]}
              onCardClick={() => {}}
              isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character
              nft={kevin}
              actions={[{ name: 'Sample', isApproved: true, onClick: () => {}, onHoverMessage: null }]}
              onCardClick={() => {}}
              isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character
              nft={mitch}
              actions={[{ name: 'Sample', isApproved: true, onClick: () => {}, onHoverMessage: null }]}
              onCardClick={() => {}}
              isSample={true}></Character>
          </SwiperSlide>
          <SwiperSlide>
            <Character
              nft={judy}
              actions={[{ name: 'Sample', isApproved: true, onClick: () => {}, onHoverMessage: null }]}
              onCardClick={() => {}}
              isSample={true}></Character>
          </SwiperSlide>
        </Swiper>
      </React.Fragment>
    );
  }
}

export default Collectibles;
