import React, { Component } from 'react';
import { IAssetConfig } from '../../models/AssetConfig';
import { INftDetails } from '../../models/NftDetails';
import { CharacterNftService } from '../../services/CharacterNftService';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import { Character } from './Characters';
import { CharacterBoost } from './CharacterBoost';

export interface ICharacterSetProps {
  nftService: CharacterNftService;
  equipTarget: IAssetConfig;
}

export interface ICharacterSetState {
  nftBoost?: INftDetails | null;
}

export class CharacterSet extends Component<ICharacterSetProps, ICharacterSetState> {
  navigationPrevRef: any;
  navigationNextRef: any;

  constructor(props: any) {
    super(props);
    this.state = {
      nftBoost: null
    };

    this.activate = this.activate.bind(this);
    this.boost = this.boost.bind(this);

    this.navigationPrevRef = React.createRef();
    this.navigationNextRef = React.createRef();
  }

  async activate(nft: INftDetails) {
    if (this.props.nftService.isEmployApproved) {
      this.props.nftService.activate(
        this.props.nftService.config.assetConstants!.assetTypeCode,
        nft,
        this.props.equipTarget
      );
    } else {
      this.props.nftService.approveActivate(this.props.nftService.config);
    }
  }

  async boost(nft: INftDetails) {
    if (this.props.nftService.isBoostApproved) {
      this.setState({ nftBoost: nft });
    } else {
      this.props.nftService.approveBoost(this.props.nftService.config);
    }
  }

  render() {
    return (
      <React.Fragment>
        <Swiper className="px-1" navigation spaceBetween={10} slidesPerView="auto" centerInsufficientSlides>
          {this.props.nftService!.nfts.map(n => (
            <SwiperSlide key={n.name + n.index}>
              <Character
                actions={[
                  {
                    name: 'Employ',
                    isApproved: this.props.nftService.isEmployApproved,
                    onClick: this.activate,
                    onHoverMessage:
                      'Consider harvesting your growing Corn before employing a character as your farm boost will be reset to 1x and your growing Corn will be temporarily reduced'
                  },
                  {
                    name: 'Boost',
                    isApproved: this.props.nftService.isBoostApproved,
                    onClick: this.boost,
                    onHoverMessage: null
                  }
                ]}
                onCardClick={() => {}}
                nft={n}
                isSample={false}></Character>
            </SwiperSlide>
          ))}
        </Swiper>
        {this.state.nftBoost != null && (
          <CharacterBoost
            nftService={this.props.nftService}
            nft={this.state.nftBoost}
            close={() => this.setState({ nftBoost: null })}></CharacterBoost>
        )}
      </React.Fragment>
    );
  }
}
